.gpt3__blog-container_article {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  background: var(--color-footer);
}

.gpt3__blog-container_article-image {
  width: 100%;
  height: 100%;
  background: var(--color-bg);
}

.gpt3__blog-container_article-image img {
  width: 100%;
  height: 100%;
}

.gpt3__blog-container_article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding: 1rem 1.5rem;
  height: 100%;
}

.gpt3__blog-container_article-content p {
  font-family: var(--font-family);
  font-size: 11.65px;
  font-weight: 700;
  line-height: 14px;
  color: white;
  margin-bottom: 10px;
}

.gpt3__blog-container_article-content button {
  font-family: var(--font-family);
  font-size: 11.65px;
  font-weight: 700;
  line-height: 14px;
  background: transparent;
  text-align: right;
  color: #fff;
  margin: 0;
  transition: all 0.3s ease;
  width: fit-content;
  float: right;
}

.gpt3__blog-container_article-content button:hover {
  font-family: var(--font-family);
  font-size: 11.65px;
  font-weight: 700;
  line-height: 25px;
  background: var(--darkBlue);
  text-align: right;
  color: #fff;
  width: 100%;
  transition: all 0.3s ease;
}

.blogBtn {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  transition: all 0.3s ease;
}

.gpt3__blog-container_article-content h3 {
  font-family: var(--font-family);
  font-size: 25.11px;
  font-weight: 800;
  line-height: 30.3px;

  color: #fff;
  margin-bottom: 5rem;
  cursor: pointer;
}

.gpt3__blog-container_article-content p:last-child {
  cursor: pointer;
}

@media screen and (max-width: 550px) {
  .gpt3__blog-container_article-content h3 {
    font-size: 18px;
    line-height: 25p;
  }
}
