@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.gpt3__blog-container_article {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  background: var(--color-footer);
}

.gpt3__blog-container_article-image {
  width: 100%;
  height: 100%;
  background: var(--color-bg);
}

.gpt3__blog-container_article-image img {
  width: 100%;
  height: 100%;
}

.gpt3__blog-container_article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding: 1rem 1.5rem;
  height: 100%;
}

.gpt3__blog-container_article-content p {
  font-family: var(--font-family);
  font-size: 11.65px;
  font-weight: 700;
  line-height: 14px;
  color: white;
  margin-bottom: 10px;
}

.gpt3__blog-container_article-content button {
  font-family: var(--font-family);
  font-size: 11.65px;
  font-weight: 700;
  line-height: 14px;
  background: transparent;
  text-align: right;
  color: #fff;
  margin: 0;
  transition: all 0.3s ease;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  float: right;
}

.gpt3__blog-container_article-content button:hover {
  font-family: var(--font-family);
  font-size: 11.65px;
  font-weight: 700;
  line-height: 25px;
  background: var(--darkBlue);
  text-align: right;
  color: #fff;
  width: 100%;
  transition: all 0.3s ease;
}

.blogBtn {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  transition: all 0.3s ease;
}

.gpt3__blog-container_article-content h3 {
  font-family: var(--font-family);
  font-size: 25.11px;
  font-weight: 800;
  line-height: 30.3px;

  color: #fff;
  margin-bottom: 5rem;
  cursor: pointer;
}

.gpt3__blog-container_article-content p:last-child {
  cursor: pointer;
}

@media screen and (max-width: 550px) {
  .gpt3__blog-container_article-content h3 {
    font-size: 18px;
    line-height: 25p;
  }
}

.gpt3__blog {
    display: flex;
    flex-direction: column;
}

.gpt3__blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 2rem;
}

.gpt3__blog-heading h1 {
    font-size: 40px;
    line-height: 40px;
    font-family: var(--font-family);
    font-weight: 800;
}

.gpt3__blog-container {
    display: flex;
    flex-direction: row;
}

.gpt3__blog-container_groupA {
    flex: 0.75 1;
    margin-right: 2rem;
}

.gpt3__blog-container_groupB {
    flex: 1 1;

    display:grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .gpt3__blog-container {
        flex-direction: column-reverse;
    }


    .gpt3__blog-container_groupA {
        margin: 2rem 0;
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article  {
       width: 48%;
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article  {
       width: 100%;
    }

    .gpt3__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}
.gpt3__features-container__feature {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-width: 180px;
  margin: 1rem;
  transition: all 0.2s ease;
}

.gpt3__features-container__feature-title {
  flex: 1 1;
  max-width: 180px;
  margin: auto;
  min-height: 60px;
  margin-bottom: 15px;
}

.gpt3__features-container__feature-title h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 22px;
  line-height: 28px;
  letter-spacing: -0.04em;
  color: #fff;
}

.gpt3__features-container__feature-title div {
  width: 50px;
  height: 3px;
  background: var(--gradient-bar);
  border-radius: 5px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 0.5rem;
  margin-left: auto;
  margin-right: auto;
}

.gpt3__features-container_feature-text {
  flex: 1 1;
  max-width: 390px;
  display: flex;
}

.gpt3__features-container_feature-text p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: var(--color-text);
}

.gpt3__features-container__feature:hover {
  transform: scale(1.2);
  transition: all 0.2s ease;
}

.gpt3__features-container__feature:hover > a > div > h1 {
  transition: all 0.2s ease;
  background: transparent;
  text-shadow: var(--pinkEnd) 6px 0 30px;
}

@media screen and (max-width: 550px) {
  .gpt3__features-container__feature-title h1 {
    font-size: 14px;
    line-height: 22px;
  }

  .gpt3__features-container_feature-text p {
    font-size: 12px;
    line-height: 20px;
  }

  .gpt3__features-container__feature {
    margin: 1rem 0;
  }
}

#features {
  color: white !important;
  margin: 4rem 3rem;
}

.gpt3__features {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.gpt3__features-heading {
  flex: 1 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;

  text-align: left;
  margin-right: 5rem;
}

.miniTitle {
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1.4;
  letter-spacing: 0.1em;
  margin: 0 auto 1.6rem;
  color: var(--clearBlue);
}

.title {
  font-size: 1.6rem;
  margin-bottom: 0.6rem;
  color: white;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

.description {
  font-size: 0.975rem;
  line-height: 1.5;
  margin: auto;
  max-width: 50%;
}

.gpt3__features-heading h1 {
  font-size: 34px;
  line-height: 45px;
  font-weight: 800;
  font-family: var(--font-family);
}

.gpt3__features-heading p {
  color: var(--color-subtext);
  font-size: 16px;
  line-height: 30px;
  font-weight: 500;
  font-family: var(--font-family);

  margin-top: 2rem;
}

.gpt3__features-container {
  flex: 1.5 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  margin-top: 5rem;
}
.featureButtons {
  display: flex;
  margin: 5rem auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 30%;
  grid-gap: 50px;
  gap: 50px;
}
.featureButtons>*{
  padding: 3px;
  border-radius: 12px;
  transition: all 0.25s ease;
}

.featuresButtonContainerAi {
  background: linear-gradient(to right, var(--darkBlue),  var(--pinkEnd));
}
.featuresButtonContainerPr {
  background: linear-gradient(to right, var(--pinkEnd),  var(--darkBlue));
}

.featureButtons>*:hover{
  transform: translateY(-6px);
  transition: all 0.25s ease;
  box-shadow: 0px 6px 30px 0px var(--purple);

}

.featureBtns {
  background-color: #040c18;
  font-size: 20px;
  border-radius: 12px;
}

@media screen and (max-width: 990px) {
  .gpt3__features {
    flex-direction: column;
  }

  .gpt3__features-heading {
    margin: 0 0 2rem 0;
  }

  .description {
    max-width: 80%;
  }
  .gpt3__features-container{
    column-count: 2 !important;
    display: block;
  }
  .gpt3__features-container> *{
    display: inline-block
  }
}

@media screen and (max-width: 550px) {
  .gpt3__features-heading h1 {
    font-size: 28px;
    line-height: 38px;
  }
  .gpt3__features-container{
    display: flex;
    flex-wrap: wrap;
    column-count: 0;
  }
}

.gpt3__footer {
