  top: 5px;
}

.dropdownButtonTitle {
  padding-top: 5px;
  padding-bottom: 5px;
  line-height: 25px;
}

.dropdownButtonTitle::after {
  display: flex;
  content: "";
  position: relative;
  margin-top: 2px;
  margin-left: -4px;
  width: 110%;
  background-color: transparent;
  border-radius: 10px;
}

.dropdownButtonTitle:hover::after {
  transform: translateY(2px);
  transition: all 200ms ease-out;
  background-color: white;
  height: 3px;
}

